/* Global */
.item-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.single-item {
  display: inline-block;
  padding: 0;
  margin: 0;
  height: 450px;
  margin: 15px 12px;
  width: 31%;
}

.single-item img {
  width: 100%;
  /* height: 350px; */
  position: relative;
}

.single-item h3 {
  font-size: 22px;
  margin: 0;
}

.item-footer {
  width: 100%;
  background-color: #4d4d4d;
  color: white;
  padding: 10px;
}

.item-footer a {
  color: white;
  margin-top: 8px;
  background-color: #ff6666;
}

.view-more {
  border: 1px solid black;
}

.form-wrapper {
  background-color: rgba(255, 255, 255, 0.6);
  width: 50%;
  /* height: 200px; */
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

